body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  background: #f5f5f5;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 550px;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}

.App {
  background: #fff;
  margin: 130px 0 40px 0;
  position: relative;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}

h1 {
  position: absolute;
  top: -155px;
  width: 100%;
  font-size: 100px;
  font-weight: 100;
  text-align: center;
  color: rgba(175, 47, 47, 0.15);
  -webkit-text-rendering: optimizeLegibility;
  -moz-text-rendering: optimizeLegibility;
  text-rendering: optimizeLegibility;
}

.new-todo {
  padding: 16px 16px 16px 60px;
  width: 81%;
  height: 30px;
  font-size: 25px;
  font-style: italic;
  font-weight: 100;
  border: none;
  color: #666666;
  background: rgba(0, 0, 0, 0.003);
  box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
}

input:focus {
  color: #000000;
  font-style: normal;
  font-weight: 100;
}

input {
  -webkit-appearance: textfield;
  background-color: white;
  -webkit-rtl-ordering: logical;
  cursor: text;
  padding: 1px;
  border-width: 2px;
  border-style: inset;
  border-color: initial;
  border-image: initial;
  outline: none;
}

input {
  text-rendering: auto;
  color: initial;
  letter-spacing: normal;
  word-spacing: normal;
  text-transform: none;
  text-indent: 0px;
  text-shadow: none;
  display: inline-block;
  text-align: start;
  margin: 0em;
  font: 400 11px system-ui;
  -webkit-writing-mode: horizontal-tb !important;
}

.normal {
  width: 100%;
  height: 100%;
  font-size: 25px;
  list-style-type: none;
  border-bottom: 1px solid #ededed;
}

.theList {
  margin: 0;
  padding: 0;
}

.normal-btn {
  height: 40px;
  width: 40px;
  position: relative;
  left: 0;
  top: 0;
  background: url(./img/unchecked.svg) no-repeat;
  cursor: pointer;
  border: none;
  margin: 10px 0;
  outline: none;
}

.normal label {
  word-break: break-all;
  padding: 15px 15px 15px 23px;
  line-height: 1.2;
  transition: color 0.4s;
  position: relative;
  top: -9px;
}

.remove {
  visibility: hidden;
}

.normal:hover .remove {
  visibility: visible;
  position: relative;
  float: right;
  top: 8px;
  right: 5px;
  bottom: 0;
  width: 40px;
  height: 40px;
  margin: auto 0;
  font-size: 30px;
  font-weight: 100;
  color: #cc9a9a;
  margin-bottom: 11px;
  transition: color 0.2s ease-out;
  cursor: pointer;
  background: none;
  outline: none;
  border: none;
}

.normal:hover .remove:hover {
  color: #d4453c;
}


.done {
  width: 100%;
  height: 100%;
  font-size: 25px;
  list-style-type: none;
  border-bottom: 1px solid #ededed;
}

.done-btn {
  height: 40px;
  width: 40px;
  position: relative;
  left: 0;
  top: 0;
  background: url(./img/checked.svg) no-repeat;
  cursor: pointer;
  border: none;
  margin: 10px 0;
  outline: none;
}

.done label {
  word-break: break-all;
  padding: 15px 15px 15px 23px;
  line-height: 1.2;
  transition: color 0.4s;
  position: relative;
  top: -9px;
  text-decoration: line-through;
  color: #d9d9d9;
}

.remove {
  visibility: hidden;
}

.done:hover .remove {
  visibility: visible;
  position: relative;
  float: right;
  top: 8px;
  right: 5px;
  bottom: 0;
  width: 40px;
  height: 40px;
  margin: auto 0;
  font-size: 30px;
  font-weight: 100;
  color: #cc9a9a;
  margin-bottom: 11px;
  transition: color 0.2s ease-out;
  cursor: pointer;
  background: none;
  outline: none;
  border: none;
}

.done:hover .remove:hover {
  color: #d4453c;
}